.img_notfound::before {
    position: absolute;
    content: 'There was some kind of error, we will definitely figure it out';
    bottom: 0;
    left: 1rem;
    right: 1rem;
    color: white;
    font-size: 24px;
    font-weight: 900;
    text-align: center;
  }
  
  @media (max-width: 450px) {
    .img-404 {
      max-width: 80% !important;
    }
    .img_notfound::before {
      bottom: -1rem;
      left: 3rem;
      right: 3rem;
      font-size: 16px;
    }
  }
  